.assistant-msg-container {
    width: clac(100% - 10px);
    height: clac(100% - 10px);
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.assistant-msg {
    /* flex-shrink: 0; */
    max-width: var(100% - 10px);
    max-height: var(100% - 10px);
    background: black;
    display: flex;
    align-items: center;
    border-radius: var(--border-radius-1);
    color: white;
    transition: 0.5s;
    overflow-y: auto;
}

.assistant-msg.hascontent {
    padding-right: 8px;
    padding-left: 2px;
    max-width: var(100% - 10px);
}

.msg-avtar {
    width: 40px;
    height: 40px;
}

.msg-avtar img {
    width: 100%;
    height: 100%;
    border-radius: var(--border-radius-1);
}

.msg-list {
    width: calc(100% - 40px);
    height: 100%;
}

.msg-item {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}